import { useEffect } from "react";

const Alert = (props) => {
    const { name = '', closeAlert = Function.prototype } = props;

    useEffect(() => {
        const timerId = setTimeout(closeAlert, 3000);

        return () => {
            clearTimeout(timerId)
        }
    }, [name]);

    return (
        <div id="toast-container">
            <div className="toast">
                {name} added to backet
            </div>
        </div>
    );
}

export { Alert };